<template>
  <el-row type="flex" class="header">
    <el-col :span="6" style="width: 290px">
      <!-- 左侧 -->
      <div class="side-bar-left">
        <h4 style="float: left; margin-top: 10px; margin-left: 5px">
          添加题目
        </h4>
        <br />
        <br />
        <subjecttype
          v-for="(item, index) in subjectData"
          :key="index"
          :data="item.data"
          :title="item.type"
        ></subjecttype>
      </div>
    </el-col>
    <el-col>
      <!-- 中间 -->
      <div class="content">
        <edittable v-on:eComponent="getComponent"></edittable>
      </div>
    </el-col>
    <el-col :span="6" style="width: 290px">
      <!-- 右侧 -->
      <div class="side-bar-right">
        <el-tabs type="card" v-model="activeName">
          <el-tab-pane label="编辑题目" name="first">
            <div
              :style="'height:' + (window_h - 195) + 'px;overflow-y:scroll;'"
            >
              <component :is="component"></component>
            </div>
          </el-tab-pane>
          <el-tab-pane label="问卷样式" name="second"></el-tab-pane>
        </el-tabs>
        <el-button
          type="primary"
          @click="save_table"
          style="
            position: absolute;
            bottom: 10px;
            left: 10px;
            width: 95%;
            height: 30px;
            line-height: 8px;
          "
          >保存问卷</el-button
        >
      </div>
    </el-col>
  </el-row>
</template>
<script>
// 引入组件
// 修改组件必填
import subjecttype from "@/components/EditPageComp/TagComponent/Subjecttype.vue";
import edittable from "@/components/EditPageComp/BodyComponent/Table.vue";
import e_head from "@/components/EditPageComp/editcomp/HeadEdit.vue";
import e_checkbox from "@/components/EditPageComp/editcomp/CheckBoxEdit.vue";
import e_radio from "@/components/EditPageComp/editcomp/RadioEdit.vue";
import e_select from "@/components/EditPageComp/editcomp/SelectEdit.vue";
import e_textbox from "@/components/EditPageComp/editcomp/TextBoxEdit.vue";
import e_picradio from "@/components/EditPageComp/editcomp/PicRadioEdit.vue";
import e_piccheckbox from "@/components/EditPageComp/editcomp/PicCheckBoxEdit.vue";
import comp from "@/utils/comp.js";
import { saveTable, getTableData, deleteImg } from "@/network/tabledata.js";
import { getSubject } from "@/network/subject.js";
export default {
  components: {
    subjecttype,
    edittable,
    "e-head": e_head,
    "e-checkbox": e_checkbox,
    "e-radio": e_radio,
    "e-select": e_select,
    "e-textbox": e_textbox,
    "e-picradio": e_picradio,
    "e-piccheckbox":e_piccheckbox
  },
  data: function () {
    return {
      //存放添加题目选项
      subjectData: [],
      //设置初始选项
      activeName: "first",
      //
      component: "",
    };
  },
  methods: {
    getComponent: function (component) {
      this.component = comp.is_editpage_comp(component);
    },
    // 保存问卷
    save_table() {
      console.log("---------------------------");
      console.log(this.$store.getters["tabledata/form"]);
      let formData = JSON.parse(
        JSON.stringify(this.$store.getters["tabledata/form"])
      );
      saveTable(formData);
      this.$message({
        message: "保存表单成功!",
        type: "success",
      });
    },
		reflesh:function(e) {
			console.log('shuaxin')
			// Cancel the event
			e.preventDefault();
			// Chrome requires returnValue to be set
			e.returnValue = "hello";
		}
  },
  computed: {
    // 获取窗口高度
    window_h() {
      return this.$store.getters["windowheight/height"];
    },
  },
  created: function () {
    sessionStorage.setItem("TagActiveName", "first");
    // 获取url上的formId，请求后台表单数据
    getTableData(this.$route.params.id).then((res) => {
      console.log("---------编辑后获取的表单数据---------");
      console.log(res.data);
      this.$store.commit("tabledata/insert", res.data);
      getSubject().then((subject_res) => {
        //设置题目
        this.subjectData = subject_res[res.data.formType];
      });
    });
    window.addEventListener("beforeunload", function(e) {
			// Cancel the event
			e.preventDefault();
			// Chrome requires returnValue to be set
			e.returnValue = "hello";
		}, true)
  },
  beforeDestroy: function () {
    let picarr = this.$store.getters["tabledata/deletePicArr"];
    for (let r in picarr) {
      for (let i in picarr[r].option) {
        deleteImg(picarr[r].option[i]);
      }
    }
		window.removeEventListener('beforeunload',function(e) {
			// Cancel the event
			e.preventDefault();
			// Chrome requires returnValue to be set
			e.returnValue = "hello";
		},false);
  }
};
</script>


<style scoped>
.header >>> .el-tabs--card > .el-tabs__header .el-tabs__nav {
  border: 2px solid #e4e7ed;
}
.header >>> .el-tabs__nav {
  margin: 0 15%;
}
.header,
.el-col,
.side-bar-left,
.side-bar-right {
  height: 100%;
}
.side-bar-left {
  width: 290px;
  background: #fafafa;
  overflow-y: scroll;
}
.content {
  background: #d9ecff;
  height: 100%;
  overflow-y: scroll;
}
.side-bar-right {
  position: relative;
  width: 290px;
  /* overflow-y: scroll; */
}
.container {
  padding: 0;
}
</style>