<template>
<div class="e-checkbox">
    <div class="text">标题</div>
    <el-input v-model="data.templateTitle" size="small"></el-input>
    <div class="text">标题描述</div>
    <el-input v-model="data.templateDesc" size="small"></el-input>
    <div class="text">选项</div>
    <div v-for="(item,index) in data.templateContent" :key="index">
        <el-input v-model="item.value" size="small" style="width:200px;display:inline-block" :placeholder="item.value"></el-input>
        <el-button 
            @click="delete_option(index)" 
            type="danger" 
            icon="el-icon-delete" 
            circle size="mini" 
            style="margin-left:5px">
        </el-button>
    </div>
    <el-row>
        <el-col :span="12" :offset="6">
            <el-button size="mini" @click="add_option">添加项</el-button>
        </el-col>
    </el-row>
</div>
</template>
<script>
import comp from "@/utils/comp.js"
import { deleteOption } from "@/network/tabledata.js";
export default {
    data:function(){
        return{
            newOption:[]
        }
    },
    methods:{
        add_option(){
            let num=comp.getNumBycircluerSort(this.data.templateContent)
            this.data.templateContent.push({'key':num,'value':'空'})
            this.newOption.push(num)
        },
        delete_option(index){
            //如果该表不是新表
            if(this.data.formTemplateId!==null&&this.data.formTemplateId!==undefined&&this.data.formTemplateId!==""){
                //定义标识
                let isHas=false
                //循环新增选项
                for(let r in this.newOption){
                    //如果是新增选项
                    if(this.newOption[r]===this.data.templateContent[index].key){
                        isHas=true
                        //删除新增列表
                        this.newOption.splice(r,1)
                        break
                    }
                }
                //如果不是新选项
                if(isHas===false){
                    //删除
                    deleteOption(this.data.formTemplateId,this.data.templateContent[index])
                }
            }
            this.data.templateContent.splice(index,1)
        }
    },
    computed:{
        data(){
            return this.$store.getters['tabledata/data']
        }
    }
}
</script>
<style scoped>
    .e-checkbox >>> .text{
        text-align: left;
        font-size: 14px;    
        margin: 20px 0 5px 0;
    }
    .e-checkbox >>> .el-input{
        margin: 5px 0;
    }
    .e-checkbox{
        padding: 0 15px;
    }
    .e-checkbox >>> div{
        display: block;
    }
</style>