<template>
  <div class="t-piccheckbox">
    <!-- 标题 -->
    <h4 style="margin-bottom: 10px">{{index}}.{{data1.templateTitle}}</h4>
    <!-- 描述 -->
    <p class="t-desc">{{data1.templateDesc}}</p>
    <el-checkbox-group v-model="data1.data" class="t-checkbox-group">

    <div v-for="(item, index) in data1.templateContent" :key="index">
      <el-row :gutter="20" v-if="index % 4 === 0">
        <el-col :span="6"
          ><div
            class="grid-content bg-purple box"
            v-if="
              data1.templateContent.length - index > 4 ||
              computedIndex(data1.templateContent.length - index)[0] === 1
            "
          >
            <el-image class="image" :src="data1.templateImgUrl[index].value" fit="fill"></el-image>
            <el-checkbox class="checkbox" :label="data1.templateContent[index]" border>
            {{data1.templateContent[index].value}}</el-checkbox
            >
          </div></el-col
        >
        <el-col :span="6"
          ><div
            class="grid-content bg-purple box"
            v-if="
              data1.templateContent.length - index > 4 ||
              computedIndex(data1.templateContent.length - index)[1] === 2
            "
          >
            <el-image class="image" :src="data1.templateImgUrl[index+1].value" fit="fill"></el-image>
            <el-checkbox class="checkbox" :label="data1.templateContent[index + 1]" border>
            {{data1.templateContent[index + 1].value}}</el-checkbox
            >
          </div></el-col
        >
        <el-col :span="6"
          ><div
            class="grid-content bg-purple box"
            v-if="
              data1.templateContent.length - index > 4 ||
              computedIndex(data1.templateContent.length - index)[2] === 3
            "
          >
            <el-image class="image" :src="data1.templateImgUrl[index+2].value" fit="fill"></el-image>
            <el-checkbox class="checkbox" :label="data1.templateContent[index + 2]" border>
            {{data1.templateContent[index + 2].value}}</el-checkbox
            >
          </div></el-col
        >
        <el-col :span="6"
          ><div
            class="grid-content bg-purple box"
            v-if="
              data1.templateContent.length - index > 4 ||
              computedIndex(data1.templateContent.length - index)[3] === 4
            "
          >
            <el-image class="image" :src="data1.templateImgUrl[index+3].value" fit="fill"></el-image>
            <el-checkbox class="checkbox" :label="data1.templateContent[index + 3]" border>
            {{data1.templateContent[index + 3].value}}</el-checkbox
            >
          </div></el-col
        >
      </el-row>
    </div>
    </el-checkbox-group>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      data1:this.data
    };
  },
  methods: {
    computedIndex(index) {
      let arr = [];
      for (let i = 1; i <= index; i++) {
        arr.push(i);
      }
      return arr;
    },
  },
  props:{
      data:{
          type:Object
      },
      index:Number
  },
};
</script>

<style scoped>
.t-piccheckbox >>> .box {
  margin: 5px;
  text-align: center;
}
.t-piccheckbox >>> .checkbox {
  width: 90%;
  min-width: 60px;
  text-align: left;
}
.t-piccheckbox >>> .image {
  width: 100px;
  height: 100px;
  min-height: 80px;
  min-width: 80px;
  text-align: left;
}
</style>